<template>
  <div class="flexible">
    <header>
      <img src="http://gw.alicdn.com/mt/TB1PNLZKXXXXXaTXXXXXXXXXXXX-750-481.jpg" alt="">
    </header>
    <section class="content">
      <div class="item-content" v-for="product in products">
        <div class="item img">
        <img :src="product.imgUrl" alt="">
        </div>
        <div class="item info">
          <div class="title">
            <a href="##">{{product.title}}</a>
          </div>
          <div class="price">
            <span>双11价</span>
            <strong>￥{{product.price}}</strong>
            <small>{{product.moneyOff}}</small>
          </div>
          <div class="tip">
            1小时内热卖{{product.hSales}}件
          </div>
          <a href="##" class="btn">马上抢！</a>
        </div>
      </div>
      <div class="item-content">
      </div>
    </section>
  </div>
</template>

<script>

const itemInfo = {
  imgUrl: 'https://placeimg.com/350/350/people/grayscale',
  title: 'Carters1年式灰色长袖连体衣包脚爬服全棉鲸鱼男婴儿童装115G093',
  price: 299.06,
  moneyOff: '满400减100',
  hSales: 5885,
};

const products = [];

for (let i = 0; i < 10; i += 1) {
  products.push(itemInfo);
}

export default {
  name: 'Flexible',
  data() {
    return {
      products,
    };
  },
};
</script>

<style lang='scss'>
  @import  "./Flexible.scss";
</style>



